.page {
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  position: relative;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
@import "__body/page__body.less";
